<template>
  <div class="user-box">
    <HeadTop title="我的资产" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="top-banner">
       <div class="title">人人都可以做主播</div>
       <div class="desc">语音直播工作室</div>
       <div class="span-btn">
          火热进行中
       </div>
    </div>
    <div class="user-item">
      <div class="title">我的代币资产</div>
      <div class="token-box flex-between">
        <div class="token-item">
        <div class="name">USDT</div>
        <div class="num">{{usdtBalance}}</div>
      </div>
      <div class="token-item">
        <div class="name">KTT</div>
        <div class="num">{{kttBalance}}</div>
      </div>
      <div class="token-item">
        <div class="name">KTM</div>
        <div class="num">{{ktmBalance}}</div>
      </div>
      </div>
    </div>
    <div class="user-item">
      <div class="title">我的业绩</div>
      <div class="nft-box flex-between">
        <!-- <div class="nft-item">
        <div class="name">全网业绩</div>
        <div class="num">{{totalPerformance}}</div>
      </div> -->
      <div class="nft-item">
        <div class="name">我的投资</div>
        <div class="num">{{investAmount}}</div>
      </div>
      <div class="nft-item">
        <div class="name">小区运营业绩</div>
        <div class="num">{{teamAmount}}</div>
      </div>
      <div class="nft-item">
        <div class="name">运营总业绩</div>
        <div class="num">{{peopleTeamAmount}}</div>
      </div>
      </div>
    </div>
     <div class="user-item">
      <div class="title">我的收益</div>
      <div class="mynft-box">
        <div class="mynft-item flex-middle">
          <div class="desc-box flex-item">
            <div class="name">待领取收益</div>
            <div class="num">收益: {{reward}}KTT</div>
          </div>
          <div class="get-btn" @click="withdrawReward">领取</div>
      </div>
      <div class="mynft-item flex-middle">
          <div class="desc-box flex-item">
            <div class="name">总收益</div>
            <div class="num">收益: {{totalBack}}KTT</div>
          </div>
      </div>
      <div class="mynft-item flex-middle">
          <div class="desc-box flex-item">
            <div class="name">已领取收益</div>
            <div class="num">收益: {{haveBack}}KTT</div>
          </div>
      </div>
      <div class="mynft-item flex-middle">
          <div class="desc-box flex-item">
            <div class="name">运营收益</div>
            <div class="num">收益: {{introAmount}}KTT</div>
          </div>
      </div>
      <div class="mynft-item flex-middle">
          <div class="desc-box flex-item">
            <div class="name">直播挂机日收益</div>
            <div class="num">收益: {{speed}}KTT/天</div>
          </div>
      </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import swapAbi from "@/utils/swapAbi";
import tokenAbi from "@/utils/abi";
import BigNumber from 'bignumber.js';
import { Toast } from 'vant';
import { Multicall } from 'ethereum-multicall';

export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      swapContract: null,
      usdtContract: null,
      kttContract: null,
      ktmContract: null,
      nftContract: null,
      usdtBalance: '0.00',
      kttBalance: '0.00',
      ktmBalance: '0.00',
      teamAmount: '0.00',
      peopleTeamAmount: '0.00',
      investAmount: '0.00',
      totalPerformance: '0.00',
      haveBack: '0.00',
      introAmount: '0.00',
      speed: '0.00',
      reward: '0.00',
      totalBack: '0.00',
      initContract: () => {
        data.swapContract = new data.web3.eth.Contract(swapAbi, config.swapAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.kttContract = new data.web3.eth.Contract(tokenAbi, config.kttAddress)
        data.ktmContract = new data.web3.eth.Contract(tokenAbi, config.ktmAddress)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getPerformance()
        data.getRewardAmount()
        data.getCommunity()
      },
       getBalance: async () => {
        let kttDecimals = await data.kttContract.methods.decimals().call()
        let usdtDecimals = await data.usdtContract.methods.decimals().call()
        let ktmDecimals = await data.ktmContract.methods.decimals().call()
        let kttBalance = await data.kttContract.methods.balanceOf(data.address).call()
        let usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call()
        let ktmBalance = await data.ktmContract.methods.balanceOf(data.address).call()
        data.kttBalance =  new BigNumber(kttBalance).dividedBy(10 ** kttDecimals).toFixed(2,1)
        data.usdtBalance =  new BigNumber(usdtBalance).dividedBy(10 ** usdtDecimals).toFixed(2,1)
        data.ktmBalance =  new BigNumber(ktmBalance).dividedBy(10 ** ktmDecimals).toFixed(2,1)
      },
      getPerformance: async () => {
        let totalPerformance = await data.swapContract.methods.totalPerformance().call()
        let userInfo = await data.swapContract.methods.getUserInfo(data.address).call()
        console.log(userInfo)
        data.totalPerformance = new BigNumber(totalPerformance).dividedBy(10 ** 18).toFixed(2)
        data.peopleTeamAmount = new BigNumber(userInfo.teamAmount).dividedBy(10 ** 18).toFixed(2)
        data.investAmount = new BigNumber(userInfo.investAmount).dividedBy(10 ** 18).toFixed(2)

        data.haveBack = new BigNumber(userInfo.haveBack).dividedBy(10 ** 18).toFixed(2)
        data.introAmount = new BigNumber(userInfo.introAmount).dividedBy(10 ** 18).toFixed(2)
        data.speed = new BigNumber(userInfo.speed).dividedBy(10 ** 18).toFixed(2)
        data.totalBack= new BigNumber(userInfo.totalBack).dividedBy(10 ** 18).toFixed(2)
      },
      getCommunity: async () => {
        let userInfo = await data.swapContract.methods.getUserInfo(data.address).call()
        if(userInfo.children.length > 1) {
         let callArr = []
         for (let index = 0; index < userInfo.children.length; index++) {
           const item = userInfo.children[index];
           callArr.push(
             { reference: 'userInfo', methodName: 'getUserInfo', methodParameters: [item]}
           )
         }
          const contractCallContext = [
            {
              reference: 'swap',
              contractAddress: config.swapAddress,
              abi: swapAbi,
              calls: callArr
            }
          ]
         const multicall = new Multicall({ multicallCustomContractAddress: config.multicallAddress,web3Instance: data.web3, tryAggregate: false });
         const res = await multicall.call(contractCallContext);
         let list = []
         let resArr = res.results.swap.callsReturnContext
         let total = 0;
         for(let i = 0; i < resArr.length;i++){
           let teamAmount = data.web3.utils.hexToNumberString(resArr[i].returnValues[9].hex)
           let investAmount = data.web3.utils.hexToNumberString(resArr[i].returnValues[2].hex)
           let num = new BigNumber(investAmount).plus(teamAmount).toFixed()
           total = new BigNumber(total).plus(num).toFixed()
           list.push(num)
         }
         console.log(list)
         let max =  list.sort((a,b) => {
              return Number(b)-Number(a);
          })[0];
         data.teamAmount = new BigNumber(total).minus(max).dividedBy(10**18).toFixed(2)
       } else {
         data.teamAmount = '0.00'
       }
      },
      getRewardAmount: async () => {
        let reward = await data.swapContract.methods.getRewardAmount(data.address).call()
        data.reward = new BigNumber(reward).dividedBy(10 ** 18).toFixed(3)
      },
      withdrawReward: async () => {
         try {
          await data.swapContract.methods.withdrawReward().send({from: data.address})
          Toast('领取成功')
           data.initData()
        } catch (error) {
          Toast('领取失败')
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.user-box{
  padding: 15px 16px;
  .top-banner{
    padding: 16px;
    background: url('~@/assets/images/item_bg.png') no-repeat;
    background-size: cover;
    border-radius: 4px;
    margin-bottom: 30px;
    .title{
      font-size: 23px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 5px;
    }
    .desc{
      font-size: 14px;
      color: rgba(255, 255, 255, 0.55);
      margin-bottom: 15px;
    }
    .span-btn{
      height: 22px;
      line-height: 22px;
      font-size: 10px;
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.8) 0%, #FFFFFF 100%);
      color: #F03C85;
      padding: 0 10px;
      border-radius: 12px;
      display: inline-block;
    }
  }
  .user-item{
    margin-bottom: 24px;
    .title{
      font-size: 16px;
      color: #fff;
      font-weight: 600;
      margin-bottom: 14px;
      position: relative;
      padding-left: 10px;
      &::after{
        content: '';
        width: 3px;
        position: absolute;
        height: 100%;
        left: 0;
        background: #723EE9;
      }
    }
    .token-box{
      flex-wrap: wrap;
    }
    .token-item{
      padding: 20px;
      color: #fff;
      margin: 5px 0;
      border-radius: 6px;
      width: 49%;
      .name{
        font-size: 16px;
        font-weight: 600;
      }
      .num{
        font-weight: 600;
        font-size: 22px;
      }
      &:nth-child(1) {
        background: url('~@/assets/images/item_1.png') no-repeat;
        background-size: cover;
        .name{
          color: #FFE3C8;
        }
      }
      &:nth-child(2) {
        background: url('~@/assets/images/item_2.png') no-repeat;
        background-size: cover;
        .name{
          color: #C3E3FE;
        }
      }
      &:nth-child(3) {
        background: url('~@/assets/images/item_2.png') no-repeat;
        background-size: cover;
        .name{
          color: #C3E3FE;
        }
      }
    }
    .nft-box{
      background: linear-gradient(-90deg, #3C3863 0%, #4A3B76 100%);
      border-radius: 4px;
      padding: 0 20px;
      flex-wrap: wrap;
      .nft-item{
        width: 50%;
         padding:20px 15px;
        .name{
          color: #9D8BD0;
          font-size: 14px;
          margin-bottom: 10px;
        }
        .num{
           color: #fff;
          font-size: 18px;
          font-weight: 600;
        }
      }
    }
    .mynft-box{
      .mynft-item{
        background: linear-gradient(-90deg, #3C3863 0%, #4A3B76 100%);
        padding: 10px;
        border-radius: 4px;
        margin-bottom: 10px;
        img{
          width: 60px;
          height: 60px;
          margin-right: 12px;
          display: block;
        }
        .desc-box{
          .name{
            font-size: 15px;
            color: #fff;
            font-weight: 600;
            margin-bottom: 5px;
          }
          .num{
            font-size: 14px;
            color: #9D8BD0;
          }
        }
        .get-btn{
          background: linear-gradient(270deg, #00DFB2 0%, #00C4DE 100%);
          height: 30px;
          border-radius: 15px;
          padding: 0 15px;
          color: #195A61;
          line-height: 30px;
          font-size: 13px;
        }
      }
    }
  }
}
</style>
